import React from 'react';
import { useAppSelector } from '../../utils/hooks';
import styles from './OrderDetails.module.css';

function OrderDetails(): JSX.Element | null {
  const { order } = useAppSelector(
    (store) => ({
      order: store.order.order,
    })
  );

  return (
    <div className={styles.container}>
      {order ? <p className="text text_type_digits-large mt-9">{order.order.number}</p> : '...'}
      <p className="text text_type_main-medium mt-8 mb-15"> </p>
      <div className={styles.wrapIcon}>
      </div>
      <p className="text text_type_main-default mt-15">   </p>
      <p className="text text_type_main-default text_color_inactive mt-2 mb-30">    </p>
    </div>
  )
}

export default OrderDetails;